@import "./header";
@import "./actions";
@import "./table";
@import "./scheduler";
@import "./pbar";
@import "./log";
@import "./filters";

body{
    font-family: Arial, Helvetica, sans-serif;
    box-sizing: border-box;
    margin: 0;
    padding: 10px;
    position: relative;
}

main{
    margin-top: 1.5rem;
    padding: 1rem;
    background: rgba(105,184,235,.3);
}

h1{
    text-align: center;
    color: #C31A21;
    font-weight: bold;
    font-size: 1rem;
    text-transform: uppercase;
    margin: 1rem 0 1.5rem;
}

h3{
    font-weight: bold;
    margin: 0;
    font-size: 1.5rem;
}

h3{
    font-weight: bold;
    margin: 0;
    font-size: 1.25rem;
}

p{
    font-size: .75rem;
    margin: 0;
}

label{
    text-transform: uppercase;
    font-size: .75rem;
}

textarea, select, input{
    box-sizing: border-box;
}

hr{
    margin: 2px 0;
}

// buttons
.btn-icon{
    background: none;
    border: none;
    border-radius: 50%;
    width: 1.5rem;
    height: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

// margin utility
.mr-s{
    margin-right: .5rem;
}

.ml-s{
    margin-left: .5rem;
}

.mx-s{
    margin: 0 .5rem;
}

//color utility
.underline-red{
    align-self: flex-start;
    background: none;
    border: none;
    color: red;
    text-decoration: underline;
}

.underline-blue{
    align-self: flex-start;
    background: none;
    border: none;
    color: blue;
    text-decoration: underline;
}

.txt-ul-blue{
    color: blue;
    text-decoration: underline;
}

// flex utility
.flex{
    display: flex;
}

.align-center{
    align-items: center;
}

.justify-between{
    justify-content: space-between;
}

// width utility
.w-120{
    width: 120px;
}

// misc

.user-switcher{
    position: absolute;
    top: 20px;
    right: 20px;
}

.count-box{
    background: #EFEEE0;
    padding: .5rem;
    display: inline-block;
    margin: .5rem 0 0 0;
    h3{
        font-weight: 400;
        font-size: .875rem;
        color: #C31A21;
        text-transform: uppercase;
        margin: 0 0 .5rem 0;
    }

}